// ----------------
// Config Sass et variables
// ----------------

// ----------------
// Valeurs de Breakpoints
// ----------------

// Hypothèse : smartphone portrait/paysage < 576px < gros smartphone paysage + tablette portrait < 992px < tablette paysage < 1330px < laptop et plus
$breakpoints: (
  sm: 576px,
  md: 992px,
  lg: 1400px,
) !default;

$small: map-get($breakpoints, sm) !default;
$medium: map-get($breakpoints, md) !default;
$large: map-get($breakpoints, lg) !default;

$header-height: 89px;
$footer-height: 121px;
$legal-height: 41px;

// ----------------
// Spacers
// ----------------

$spacer: 1rem;
$spacers: (
  '0': 0,
  // none
    '1': 1px,
  // mono
    '2': 0.125rem,
  // nano
    '4': 0.25rem,
  // micro
    '8': 0.5rem,
  // mini
    '12': 0.75rem,
  // small
    '16': 1rem,
  // medium
    '20': 1.25rem,
  // large
    '24': 1.5rem,
  // wide
    '36': 2.25rem,
  // ultra
    'auto': auto,
);

$spacer-none: map-get($spacers, '0');
$spacer-mono: map-get($spacers, '1');
$spacer-nano: map-get($spacers, '2');
$spacer-micro: map-get($spacers, '4');
$spacer-mini: map-get($spacers, '8');
$spacer-small: map-get($spacers, '12');
$spacer-medium: map-get($spacers, '16');
$spacer-large: map-get($spacers, '20');
$spacer-wide: map-get($spacers, '24');
$spacer-ultra: map-get($spacers, '36');

// ----------------
// Grid layout
// ----------------

$grid-columns: 24 !default;

$gaps: (
  '0': 0,
  '5': 0.313rem,
  '10': 0.625rem,
  '16': 1rem,
  '20': 1.25rem,
  '36': 2.25rem,
) !default;

// ----------------
// Fonts
// ----------------

// Familles de fonts
$font-family-base: sans-serif;
$font-family-headings: sans-serif;
$font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas, monospace;
$line-height-base: 1.5;

// Font sizes
$font-size: (
  '10': 0.625rem,
  '11': 0.6875rem,
  '12': 0.75rem,
  '13': 0.813rem,
  '14': 0.875rem,
  '15': 0.938rem,
  '16': 1rem,
  '17': 1.063rem,
  '18': 1.125rem,
  '20': 1.25rem,
  '22': 1.375rem,
  '24': 1.5rem,
  '26': 1.625rem,
  '28': 1.75rem,
  '30': 1.875rem,
  '32': 2rem,
  '34': 2.125rem,
  '36': 2.25rem,
  '38': 2.375rem,
  '42': 2.625rem,
  '60': 3.75rem,
  'base': 1rem,
  'inherit': inherit,
);

$font-size-html: 100%;
$font-size-base: 1rem;

// Graisses des Fonts
$weight-200: 200;
$weight-300: 300;
$weight-400: 400;
$weight-500: 500;
$weight-600: 600;
$weight-700: 700;

// ----------------
// Couleurs
// ----------------

// Variables niveaux de gris

$gray-100: #f8f9fa;
$gray-200: #e9ecef;
$gray-300: #dee2e6;
$gray-400: #ced4da;
$gray-500: #adb5bd;
$gray-600: #6c757d;
$gray-700: #495057;
$gray-800: #343a40;
$gray-900: #212529;
$white: #ffffff;
$black: #34495e;

// Variables couleurs globales

$body-color: $black;
$body-bg: $white;

$primary: #b1afff; // Couleur dominante. Elle peut s'appliquer à la fois à des surfaces ou à des contenus.
$primary-light: #c9c8ff;
$primary-background: #efeeff;
$primary-accent: #e55039; // Modifieur de couleur dominante (survol, bordure)
$on-primary: $white; // Contenus (texte, lien, icône) affichés par-dessus une couche $primary

$secondary: #008cba; // Couleur d'accentuation. Elle s'applique généralement aux liens, mais peut s'appliquer à la fois à des surfaces ou à des contenus.
$secondary-accent: #027da7; // Modifieur de couleur d'accentuation (survol, bordure)
$on-secondary: $white; // Contenus affichés par-dessus une couche $secondary

// Variables couleurs statuts

$success-bg: #dff0d9;
$success-border: #d6e9c6;
$success: #3c763d;
$success-hover: #275c28;

$info-bg: #d9edf7;
$info-border: #bce8f1;
$info: #0d7192;
$info-hover: #005e7d;

$warning-bg: #fcf8e3;
$warning-border: #faebcc;
$warning: #8a6d3b;
$warning-hover: #654f29;

$error-bg: #fff2f2;
$error-border: #ebccd1;
$error: #e3224a;
$error-hover: #a00d10;

// Links
$link-decoration: underline;
$link-decoration-hover: underline;

// Border radius
$radius-none: 0;
$radius-small: 0.5rem;
$radius-medium: 1rem;
$radius-large: 20rem;
$radius-circle: 50%;

// ----------------
// Liste des propriétés utilitaires (note display: grid se trouve dans Grillade)
// ----------------
