/* Global container */
.layout-maxed {
  display: grid;
}
@media (min-width: 576px) {
  .layout-maxed {
    grid-template-columns: minmax(0.75rem, 1fr) minmax(auto, 576px) minmax(0.75rem, 1fr);
  }
}
@media (min-width: 992px) {
  .layout-maxed {
    grid-template-columns: minmax(0.75rem, 1fr) minmax(auto, 992px) minmax(0.75rem, 1fr);
  }
}
@media (min-width: 1400px) {
  .layout-maxed {
    grid-template-columns: minmax(0.75rem, 1fr) minmax(auto, 1400px) minmax(0.75rem, 1fr);
  }
}

/* Center all children */
.layout-maxed > * {
  grid-column: 2;
}

/* Hero box  */
.layout-hero {
  grid-column: 1/-1;
  display: grid;
  grid-template-columns: inherit;
}

.layout-hero > * {
  grid-column: 2;
}

/* Hero image */
.layout-hero-img {
  grid-column: 1/-1;
  justify-self: center;
  max-width: 100%;
}

